/**
 * Vue的插件，用来获取和设置localStorage存储
 **/

let local={
	save(key,value){
		localStorage.setItem(key,JSON.stringify(value))
	},
	fetch(key){
		return JSON.parse(localStorage.getItem(key)) || {}
	}
}

export default {
	/*利用这个方法可以在vue实例原型上写方法，属性，子vue实例对象都可以使用该方法*/
	
	install:function(vm){
		//console.log(vm);//vue实例对象
		vm.prototype.$local=local;//放在vue实例的原型里面，都可以访问
	}
	
}

















