import Vue from 'vue'
import VueRouter from 'vue-router'

//作为vue的插件
Vue.use(VueRouter)

import home from "@/components/home"
import header from "@/components/header"
import Login from "@/components/login"

//import layout from "@/views/layout"
//import Project from "@/views/backend/project"
//import Workbench from "@/views/backend/workbench"
//import Doc from "@/views/backend/doc"


//require.ensure(依赖,回调函数,[chunk名字])//代码分割
let layout=(resolve,reject)=>{
	return require.ensure([],()=>{
		resolve(require("@/views/layout"))
	})
}
let Project=(resolve)=>{
	return require.ensure([],()=>{
		resolve(require("@/views/backend/project"))
	})
}
let Workbench=(resolve)=>{
	return require.ensure([],()=>{
		resolve(require("@/views/backend/workbench"))
	},"abc")//相当于标识，Workbench与Doc打包在一起
}

/*let Doc=(resolve)=>{
	return require.ensure([],()=>{
		resolve(require("@/views/backend/doc"))
	},"abc")
}*/

let Doc=(resolve)=>{//只能单个加载，不能合并
	return import("@/views/backend/doc")
}





let router=new VueRouter({
	mode:'history',               /*history模式*/
	linkActiveClass: 'is-active', /*nav里的高亮显示*/
	routes:[
		{
			path:'/',
			name:'Home',
			component:home
		},
		{
			path:'/management',//后台管理入口  嵌套路由
			component:layout,
			children:[
				{
					path:'/project',
					name:'Project',
					component:Project,
					meta:{
						nessLogin:true
					}
				},
				{
					path:'/workbench',
					name:'Workbench',
					component:Workbench,
					meta:{
						nessLogin:true
					}
				},
				{	
					path:'/doc',
			        name:'Doc',
			        component:Doc,
			        meta: {
			          nessLogin: false
			    	}
					
				}
			]
		},
		{
			path:'/header',
			name:'Header',
			component:header
		},
		{
			path: '/login',
	      	name: 'Login',
	      	component: Login
		},
		{
			path:'*',
			redirect:'/'        //重定向
		}
	]
})

//全局路由导航钩子函数 
router.beforeEach((to,from,next)=>{
	
	/*
	 	父子路由信息对象中只要有一个需要登录就应该登录，然后看miaov里面login是否已经登录，login为false代表没有登录，就跳转到登录页面
	 	console.log(to.matched);//嵌套路由，父子路由信息对象
	*/
	
	let bl=to.matched.some((item)=>{ //一个为true都为true，为true则要判断是否登录
		return item.meta.nessLogin
	})//父子路由里面的nessLogin，看看是否需要登录
	
	if(bl){
		let info=router.app.$local.fetch("miaov")//router.app 根实例对象vm
		if(info.login){ // 已经登录
			next()
		}else{          //未登录，跳转到登录页面
			router.push({
				path:'/login',
				query:{//用于登录之后继续跳转
					redirect:to.path.slice(1)//斜杠去掉
				}
			})
		}
	}else{
		next()
	}
	
})

	
export default router;



























