<template>
  <div class="main">
  	<!--头部-->
  	<div class="home-header">
  		<img src="../assets/miaov.png" alt="" />
  		<div class="portrait">
  			<router-link to="/login" tag="span" v-if="!isLogin">登录</router-link>
  			<img src="../assets/portrait.png" alt="" v-if="isLogin" />
  		</div>
  	</div>
  	
  	<div class="phrase">一起学习vue-router</div>
  	
  	<div class="btns">
  		<router-link to="/project" tag="div" class="trans">我的项目</router-link>
  		<router-link to="/doc" tag="div" class="trans">我的文档</router-link>
  	</div>
  	
  </div>
</template>

<script>
    export default {
    	name:'home',
    	data(){
    		return {
    			isLogin:false
    		}
    	},
    	created(){
    		//Vue实例生成之后，拿到登录信息里面的login，改变data里的isLogin，进行登录文字和图像的切换
    		let info=this.$local.fetch('miaov')
    		this.isLogin=info.login
    	}
    }
</script>
<style>

</style>



