<template>
  <div class="clearFix el-menu--dark">
  	<!--全局导航-->
  	<ul class="el-menu menu-horizontal el-menu--dark el-menu--horizontal header-nav">
  		<router-link class="el-menu-item logo" to='/' exact tag="li" >
  			<img src="../assets/miaov.png">
  		</router-link>
  		<router-link class="el-menu-item" to='/project' tag="li">
  			<i class="fa fa-home"></i>
        	我的项目
  		</router-link>
  		<router-link to='/workbench' class="el-menu-item"  tag="li">
        <i class="fa fa-code"></i>
        	工作台
      </router-link>
      <router-link to="/doc" class="el-menu-item" tag="li">
        <i class="fa fa-book"></i>
       	 文档
      </router-link>
  	</ul>
  	
  	<!--登出部分-->
  	<div class="user-info-box" v-show="isLogin"><!--没有登录就隐藏-->
      <div class="el-submenu__title">
        <img src="../assets/portrait.png">
        <span>{{userName}}</span>
      </div>
      <div class="header-menu">
        <ul class="menu-nav">
          <li class="el-menu-item" @click="loginOut">登出</li>
        </ul>
      </div>
    </div>
    
  </div>
</template>

<script>
    export default {
    	name:'Header',
    	data(){
    		return {
    			userName:'',
    			isLogin:false
    		}
    	},
    	created(){
    		//更新数据userName和isLogin
    		let info=this.$local.fetch('miaov')
    		this.isLogin=info.login
    		this.userName=info.userName
    	},
    	methods:{
    		loginOut(){
    			//登出的时候清空localStorage，同时跳转到login页面
    			this.$local.save('miaov',null)
    			this.$router.push('/login')
    		}
    	}
    }
</script>
<style>

</style>



