<template>
  <div class="login">
    <div class="login-box">
    	
      <h2>登录</h2>
      
      <form @submit.prevent="sendLogin" autocomplete="off">
        <div><input placeholder="请输入用户名" type="text" name="user" ref="userInput" v-model="userName" /></div>
        <div><input placeholder="请输入密码" type="password" name="password" /></div>
        <div class="login-btn"><input type="submit" value="一键登入" /></div>
      </form>
      
      <div class="back-index">
        <router-link  to="/">首页>></router-link>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
    	name:'login',
    	data(){
    		return {
    			userName:''
    		}
    	},
    	methods:{
    		sendLogin(){
    			//登录
    			
    			if(this.userName){
	    				let userName=this.$refs.userInput.value
		    			this.$local.save("miaov",{//存储登录状态和用户名
		    				login:true,
		    				userName:userName
		    			})
		    			
		    			let redirect=this.$route.query.redirect
		    			
		    			if(!redirect){//要是redirect不存在，默认去project页面(直接登录的时候，redirect不存在)
		    				redirect='project'
		    			}
		    			this.$router.push({ //跳转页面
		    				path:'/'+redirect
		    			})
    			}else{
    				  alert("请输入用户名")
    			}
    			
    		}
    	}
    }
</script>
<style>

</style>



